import React, { FunctionComponent, useRef } from "react";
import styled from "styled-components";
import defaultTheme from "../../theme/theme";

interface Props {
  /**
   * Addition classes
   */
  className?: string;
  /**
   * Name of checkbox. You should alway assign a name
   *
   * @requires true
   */
  name?: string;
  /**
   * Value used when you'll post data
   *
   * @default
   */
  value?: string;
  /**
   * State of checked
   *
   * @default false
   */
  checked?: boolean;
  /**
   * Disable the toogle
   *
   * @default false
   */
  disabled?: boolean;
  /**
   * Trigged when the checkbox change
   *
   * @param {React.ChangeEvent} e
   */
  onToggle?: (e: React.ChangeEvent) => void;
  /**
   * Trigged when the toggle is on the left.
   */
  onRight?: () => void;
  /**
   * Trigged when the toggle is on the right.
   */
  onLeft?: () => void;
  /**
   * Default Background color
   */
  backgroundColor?: string;
  /**
   * Background color when the toggle turned on
   */
  backgroundColorRight?: string;
  /**
   * Background color when the toggle is disabled
   *
   */
  backgroundColorDisabled?: string;
  /**
   * Radius of container
   *
   * @default 8px
   */
  backgroundRadius?: string;
  /**
   * Border radius of knob
   */
  radius?: string;
  /**
   * Color knob
   */
  color?: string;
  /**
   * Color knob when turned on
   */
  colorRight?: string;

}

const ToggleContainer = styled.label``;

const ToggleBase = styled.div<Props>`
  position: relative;
  box-sizing: border-box;
  display:inline-grid;
  align-items: center;
  justify-items: center;
  width: 72px;
  height: 32px;
  vertical-align: top;
  
  input[type="checkbox"]  {
      position: absolute;
      margin-left: -9999px;
      visibility: hidden;
      
    // off state
    & + label {
      display:inline-grid;
      box-sizing: border-box;
      align-items: center;
      justify-items: center;
      outline: none;
      user-select: none;
      width: 72px;
      height: 32px;
      background-color: ${p => p.backgroundColor || (p.theme && p.theme.backgroundColor) || defaultTheme.backgroundColor};
      border:none;
      border-radius:${p => p.backgroundRadius || (p.theme && p.theme.backgroundRadius) || defaultTheme.backgroundRadius}
      cursor:pointer;
      transition : background ease-out 0.3s;
      
      &:after {
        display:inline-block;
        position:absolute;
        content: "";
        width: 26px;
        height: 26px;
        left:3px;
        border-radius: ${p => p.radius || (p.theme && p.theme.radius) || defaultTheme.radius};
        background-color: ${p => p.color || (p.theme && p.theme.color) || defaultTheme.color};
        box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.04), 0 1px 8px 0 rgba(0, 0, 0, 0.24);
        transition : margin ease-out 0.6s, background ease-out 0.6s;
      }
    }
    
    // on state
    &:checked {
      & + label {
        background-color: ${p => p.backgroundColorRight || (p.theme && p.theme.backgroundColorRight) || defaultTheme.backgroundColorRight};
        
        &:after {
          margin-left: 40px;  
          transition : margin ease-out 0.2s, background ease-out 0.2s;
          background-color: ${p => p.colorRight || (p.theme && p.theme.colorRight) || defaultTheme.colorRight};
        }
      }
      
      &:disabled {
        & + label {
          background-color: ${p => p.backgroundColorDisabled || (p.theme && p.theme.backgroundColorDisabled) || defaultTheme.backgroundColorDisabled};
          &:after {
            box-shadow: none;
          }
        }
      }
    }
    
    // disabled
    &:disabled {
      & + label {
        background-color: ${p => p.backgroundColorDisabled || (p.theme && p.theme.backgroundColorDisabled) || defaultTheme.backgroundColorDisabled};
        cursor:default;
        &:after {
          box-shadow: none;
        }
      }
    }
  }
`;

const Toggle: FunctionComponent<Props> = props => {
  const inputRef = useRef(null);

  const {
    className,
    name,
    checked = false,
    disabled = false,
    value = "",
    onToggle = () => true,
    onRight = () => true,
    onLeft = () => true,
    ...others
  } = props;

  const cs = ["react-toggle", className || ""].join(" ");

  const onChangeHandler = (e: React.ChangeEvent) => {
    if (!!onToggle) {
      const i: any = inputRef.current;
      console.log("onToggle", i.checked);
      onToggle(e);

      if (i && i.checked) {
        onRight();
      } else {
        onLeft();
      }
    }
  };

  return (
    <ToggleBase className={cs} {...others}>
      <input
        ref={inputRef}
        onChange={onChangeHandler}
        type={"checkbox"}
        defaultChecked={checked}
        id={name}
        name={name}
        value={value}
        disabled={disabled}
      />
      <ToggleContainer htmlFor={name} />
    </ToggleBase>
  );
};

export default Toggle;
